/* AQUI VA LA TABLET DE MANERA VERTICAL */
@media (max-width: 768px) {
*{
  background-color: #2A324B;
}
.App {
  text-align: center;
  color: aliceblue;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}


.App-header {
  background-color: #2A324B;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
}
/* AQUI VA LA TABLET DE MANERA HORIZONTAL*/
@media screen and (min-width: 768px) and (max-width: 1024px) {
  *{
    background-color: #2A324B;
  }
  .App {
    text-align: center;
    color:bisque;
  }
  
  .App-logo {
    height: 40vmin;
    pointer-events: none;
  }
  
  
  .App-header {
    background-color: #2A324B;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .App-link {
    color: #61dafb;
  }
  }
